'use client';

import Link from 'next/link';
import './Header.scss';
import { AppRoutes } from '../../utils/routes';
import { useTheme } from '../../hooks/useTheme';
import { BsSunFill, BsMoonFill } from 'react-icons/bs';
import { useEffect, useState } from 'react';
import Image from 'next/image';
import LanguageSwitcher from '../LanguageSwitcher/LanguageSwitcher';
import { useTranslations } from 'next-intl';

const Header = () => {
  const t = useTranslations();
  const navButtons = () => (
    <ul className="nav_button">
      <li>
        <Link href={AppRoutes.HOME_ROUTE} className="nav-link">
          <i className="bi bi-house"></i> {t('header.home')}
        </Link>
      </li>
      <li>
        <Link href={AppRoutes.ABOUT_ROUTE} className="nav-link">
          <i className="bi bi-info-circle"></i> {t('header.about')}
        </Link>
      </li>
    </ul>
  );
  const { theme, toggleTheme } = useTheme();
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
  }, []);

  if (!mounted) return null;

  return (
    <header className="header">
      <Link href={AppRoutes.HOME_ROUTE} className="header__logo-link">
        <Image
          src="/graduation-cap.png"
          alt="Site Icon"
          className="header__logo-icon"
          width={32}
          height={32}
        />
        <span className="header__app-name">{t('header.appName')}</span>
      </Link>
      <div style={{ display: 'flex', alignItems: 'center' }}>
        <nav>{navButtons()}</nav>
        <div
          className="toggle-theme"
          onClick={toggleTheme}
          aria-label="Toggle theme"
        >
          {theme === 'light' ? <BsSunFill /> : <BsMoonFill />}
        </div>
        <LanguageSwitcher />
      </div>
    </header>
  );
};

export { Header };
